.home{
    max-width: width 1500px;
    margin-right: auto;
    margin-left: auto;
}
.home__row{
    display:flex;
    z-index: 1;
    margin-right: 5px;
    margin-left: 5px;
}
.home__image{
    width:100%;
    height:500px;
    mask-image: linear-gradient(to bottom,rgb(0,0,0,1),rgb(0,0,0,0));
    z-index:-1;
    margin-bottom:-150px;
}