.header{
    background-color:rgb(37, 21, 51);
    display: flex;
    align-items: center;
    position: sticky;
    top:0;
    z-index: 100;
}
.header__image{
    width:150px;
    margin-left:20px;
    margin-right:20px;
    margin-top:18px;
}
.header__link{
    color:white;
}
.header__option{
    display: flex;
    flex-direction: column;
    margin-right: 10px;
    margin-left: 10px;
}
.header__search{
    display:flex;
    flex:1;
    border:black;
}
.header__searchInput{
    height:12px;
    padding: 10px;
    border: black;
    width:100%; 
}
.header__searchIcon{
    background:rgb(160, 137, 9);
    height: 10px;
}