.product{
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-items: flex-end;
    min-width:100px;
    max-height: 400px;
    width:100%;
    z-index: 1;
}


.product__rating{
    display:flex;
    flex-direction: row;
}

.product>img{
    max-height:200px;
    width:100%;
    object-fit:contain;
    margin-bottom:15px;
}

.product>button{
    background-color: rgb(243, 200, 11);
}